@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-Div-Main {
  background-image: url(/GoRestAPI_Front/static/media/background.07c38c7b.png);
  min-height: 100vh;
  min-width: 100wh;
  padding-bottom: 2%;
}

.App-Col-Title {
  margin-top: 5%;
  margin-bottom: 2%;
}

.App-Title {
  font-family: "Nunito";
  color: rgb(5, 138, 176) !important;
  margin-bottom: 0 !important;
}

table {
  box-shadow: 0px 0px 5px 1.5px rgba(0, 0, 0, 0.75);
  border-radius: 10%;
}

.UserModal-Info {
  margin-top: 2%;
  margin-bottom: 1%;
  border-left: 1px solid rgba(0, 0, 0, 0.3);
  padding-left: 1%;
}

.UserModal-Button {
  background-color: rgb(5, 138, 176) !important;
  border-color: rgb(5, 138, 176) !important;
}

